import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import InputAdornment from '@material-ui/core/InputAdornment';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import moment from 'moment';
import { addPayment } from '../store/actions/paymentActions';
import { connect } from 'react-redux';
import { firestoreConnect } from 'react-redux-firebase';
import { compose } from 'redux';

const styles = theme => ({
    closeButton: {
        position: 'absolute',
        right: theme.spacing(1),
        top: theme.spacing(1),
        color: theme.palette.grey[500],
    },
    button: {
        float: 'right',
        margin: theme.spacing(2, 0)
    },
    buttonSave: {
        marginRight: theme.spacing(1)
    },
    formControl: {
        width: '100%'
    },
});


class PaymentDialog extends Component {

    constructor(props) {
        super(props);
        this.state = {
            form: {
                monthName: moment(moment(), 'M').format('MMMM'),
                year: moment().year(),
                rentAmount: 0,
                prevUnit: 0,
                currentUnit: 0,
                unitConsumed: 0,
                electricBill: 0,
                total: 0
            },
            tenantName: '',
            tenantId: '',
            startingUnit: 0,
            chargePerUnit: 0,
            prevPayments: []
        }
    }


    handleTenantSelectChange = (event) => {
        this.setState({ tenantName: event.target.value });
    }

    isEmpty = inputObject => {
        return Object.keys(inputObject).length === 0;
    };

    setTenantId = (tenantId) => {
        const { tenants } = this.props;
        const tenantObj = tenants.filter(_ => _.id === tenantId)[0];
        let prevPayments = [];
        let startingUnit = 0;

        if (tenantObj.payments) {
            for (let i in tenantObj.payments) {
                prevPayments.push(tenantObj.payments[i]);
            }
        }

        if (this.isEmpty(tenantObj.payments)) {
            startingUnit = tenantObj.startingUnit;
        } else {
            startingUnit = tenantObj.payments[0].currentUnit;
        }

        this.setState({
            tenantId,
            form: { ...this.state.form, rentAmount: tenantObj.rentAmount, prevUnit: startingUnit },
            chargePerUnit: tenantObj.chargePerUnit,
            startingUnit,
            prevPayments
        });
    }

    handleChange = (event) => {
        const { name, value } = event.target;
        this.setState({
            form: { ...this.state.form, [name]: value }
        });
    }

    handleSubmit = (event) => {
        event.preventDefault();
        const newPaymentArr = [{ ...this.state.form }, ...this.state.prevPayments];
        this.props.addPayment(newPaymentArr, this.state.tenantId);
        this.props.handleDialogClose();
    }

    handleUnitChange = (event) => {
        const { name, value } = event.target;
        const { startingUnit, chargePerUnit } = this.state;
        const { rentAmount } = this.state.form;
        const unitConsumed = (value - startingUnit);
        const electricBill = (unitConsumed * chargePerUnit);
        const total = electricBill + rentAmount;
        this.setState({
            form: {
                ...this.state.form,
                [name]: Number(value),
                unitConsumed,
                electricBill,
                total
            }
        });

    }

    render() {
        const { tenantName, startingUnit, chargePerUnit } = this.state;
        const { currentUnit, rentAmount, total, unitConsumed, electricBill } = this.state.form;
        const { classes, payment, tenants, handleDialogClose } = this.props;

        return (
            <form onSubmit={this.handleSubmit}>
                <Grid container spacing={2}>
                    <Grid item xs={12} md={6} lg={6}>
                        <FormControl className={classes.formControl}>
                            <InputLabel id="demo-simple-select-label">Tenant</InputLabel>
                            <Select
                                labelId="demo-simple-select-label"
                                id="demo-simple-select"
                                color="secondary"
                                value={tenantName}
                                onChange={this.handleTenantSelectChange}
                            >
                                {
                                    tenants && tenants.map(tenant => {
                                        return (
                                            <MenuItem value={tenant.name} key={tenant.id} onClick={() => this.setTenantId(tenant.id)}>{tenant.name}</MenuItem>
                                        )
                                    })
                                }
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={12} md={6} lg={6}>
                        <TextField
                            autoFocus
                            type="number"
                            color="secondary"
                            variant="standard"
                            margin="dense"
                            id="amount"
                            name="rentAmount"
                            label="Amount"
                            value={rentAmount}
                            fullWidth
                            readOnly
                            InputProps={{
                                startAdornment: <InputAdornment position="start">â‚¹</InputAdornment>,
                            }}
                        />
                    </Grid>
                    <Grid item xs={6} md={6} lg={6}>
                        <TextField
                            type="number"
                            color="secondary"
                            variant="standard"
                            margin="dense"
                            id="prevUnit"
                            name="prevUnit"
                            label="Previous Unit*"
                            value={startingUnit}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={6} md={6} lg={6}>
                        <TextField
                            type="number"
                            color="secondary"
                            variant="standard"
                            margin="dense"
                            id="currentUnit"
                            name="currentUnit"
                            label="Current Unit"
                            value={currentUnit}
                            onChange={this.handleUnitChange}
                            fullWidth
                        />
                    </Grid>
                    <Grid item xs={6} md={6} lg={6}>
                        <TextField
                            color="secondary"
                            variant="standard"
                            margin="dense"
                            id="unitConsumed"
                            name="unitConsumed"
                            label="Unit Consumed"
                            value={unitConsumed}
                            helperText={`x ${chargePerUnit}`}
                            fullWidth
                            readOnly
                        />
                    </Grid>
                    <Grid item xs={6} md={6} lg={6}>
                        <TextField
                            color="secondary"
                            variant="standard"
                            margin="dense"
                            id="electricBill"
                            name="electricBill"
                            label="Electric Bill"
                            value={electricBill}
                            InputProps={{
                                startAdornment: <InputAdornment position="start">â‚¹</InputAdornment>,
                            }}
                            fullWidth
                            readOnly
                        />
                    </Grid>
                    <Grid item xs={6} md={6} lg={6}>
                        <Typography variant="h6">Total: â‚¹{total}  </Typography>
                    </Grid>
                    <Grid item xs={12} md={12} lg={12}>
                        <div className={classes.button}>
                            <Button
                                type="submit"
                                variant="contained"
                                color="secondary"
                                className={classes.buttonSave}
                                size="small"
                            >
                                Save
                            </Button>
                            <Button
                                variant="outlined"
                                color="secondary"
                                size="small"
                                onClick={() => handleDialogClose()}
                            >
                                Cancel
                            </Button>
                        </div>
                    </Grid>
                </Grid>
            </form>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        payment: state.firestore.ordered.payment,
        tenants: state.firestore.ordered.tenants
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        addPayment: (paymentObj, tenantId) => dispatch(addPayment(paymentObj, tenantId))
    }
}


export default withStyles(styles, { withTheme: true })(
    compose(
        connect(mapStateToProps, mapDispatchToProps),
        firestoreConnect([
            { collection: 'payment' },
            { collection: 'tenants' }
        ])
    )(PaymentDialog)
);